:root {
    --green-primary: #00AE83;
}

body {
    margin: 0;
    padding: 0;
  }

.container-fluid {
padding-top: 10vh;
padding-bottom: 10vh;
}

.hero {
    background: url(images/herobg.svg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 500px;
    color: var(--green-primary);
    padding-top: 25vh;
    padding-bottom:105vh;
  }

.first-cta {
    background-image: url(images/bgwithhills.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 500px;
  }
  .final-cta {
    background: linear-gradient(rgba(0,0,0,.7), rgba(0,0,0,.7)), url(images/bglastsection.png);
    background-position: right;
    background-size: cover;
    background-repeat: no-repeat;
    height: 500px;
  }
  
  /* buttons */

  .green-fill {
    text-decoration: none;
    color: white;
    background-color: var(--green-primary);
    padding: 1rem 2.2rem;
    border-radius: 2rem;
    font-weight: 900;
  }

  .green-fill:hover {
    color: var(--green-primary);
    border: 3px solid var(--green-primary);
    background-color: transparent;
  }